import React from "react";
import Progress from "react-progressbar";
import styles from "../styles/components/_progressBar.scss";

/**
 * Progress bar component
 * @param {*} props 
 */
const ProgressBar = (props) => {
    const percentage = props.counter / props.total * 100;

    return (
        <div className={styles.qProgressInfo}>
            {/* <span id='qProgressText' className={styles.qProgressText'>{props.counter} / {props.total}</span> */}
            <div className={styles.qProgress}>
                <div className={styles.qProgressbarContainer}>
                    <Progress completed={percentage} color={'#DDF1DD'} height='2vw' />
                </div>
            </div>
        </div>
    );
}

export default ProgressBar;