import React from 'react';
import axios from 'axios';
import styles from "../styles/components/_quizResult.scss";

export default class QuizResult extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            ip: '',
            link: this.props.product.Link_produs
        };
    }

    getIp = () => {
        axios.get('https://jsonip.com')
            .then(res => {
                // console.log(res.data.ip);
                this.setState({ ip: res.data.ip});
                this.counterAjaxCall();
                console.log(res);
            })
            .catch(function (error) {
                console.log(error);
            });
    }

    counterAjaxCall = () => {
        console.log(this.state.ip);
        axios.post('https://socialeyes20180319122912.azurewebsites.net/api/Counter?ip=' + this.state.ip + '&link=' + this.state.link)
        .then(function (response) {
            console.log(response);
        })
        .catch(function (error) {
            console.log(error);
        });
    }

    render(){
        return (
            <div className={styles.resultGroup}>
                <img className={styles.resultProductPic} src={this.props.product.Link_imagine} alt=""/>
                <a className={styles.resultProductTitle} target={'_blank'} href={this.props.product.Link_produs} onClick={this.getIp}>{this.props.product.Denumire}</a>
                <span className={styles.resultProductPrice}>{this.props.product.Pret} lei</span>
            </div>
        );
    }
}
