import React from 'react';
import axios from 'axios';
import QuizResult from './QuizResult';
import styles from "../styles/components/_quizResults.scss";

export default class QuizResults extends React.Component {

    constructor(props) {
        super(props);
        // quizSugestions = `https://www.tiparo.ro/eticheta-produs/${props.result.trim().replace(/ /g, '-')}`;
        this.state = {
            socialQuizResult: '',
            loading: true,
        };
    }
    
    quizSugestions = `https://www.tiparo.ro/eticheta-produs/${this.props.result.trim().replace(/ /g, '-')}`;

    componentDidMount() {
        console.log(this.props.result);

        axios.get(`http://socialeyes20180319122912.azurewebsites.net/api/QuizResults?result=${this.props.result.trim()}`)
            .then(res => {
                const socialQuizResult = res.data;
                this.setState({socialQuizResult, loading: false});
                // console.log(this.state.socialQuizResult);
            })

        // console.log(this.state.socialQuizResult);
    }

    render() {
        console.log(this.state.socialQuizResult);
        const { socialQuizResult, loading } = this.state;
        return (
            <div className={styles.quizResults}>
                Va sugerem produsele <a href={this.quizSugestions}>acestea</a>:
                <div className={styles.quizResult}>
                    {
                        !loading && socialQuizResult.map((option, index) => (//change this also!!!!!!!!!!!!!!
                                <div key={index}>
                                    <QuizResult product={option} />
                                    {/* {option} */}
                                </div>
                            ))
                        // this.state.socialQuizResult.map((option, index) => (//change this also!!!!!!!!!!!!!!
                        //     <div key={index}>
                        //         {/* console.log({option}); */}

                        //     </div>
                        // ))
                    }
                </div>

                {/* <iframe className={styles.quizIframeSugestions} src={quizSugestions}></iframe> */}
            </div>
        )
    }

}


