import React from "react";
import axios from 'axios';
import LoadingSpinner from './LoadingSpinner';
import ProgressBar from './ProgressBar';
import QuizAnswer from './QuizAnswer';
import QuizQuestion from './QuizQuestion';
import QuizResults from './QuizResults';
import styles from "../styles/components/_quizContainer.scss";


export default class RenderSocialQuiz extends React.Component {

    state = {
        error: null,
        title: 'SocialQuiz',
        loading: true,
        socialQuizResponse: '',
        questionContent: '',
        answerContent: '',
        answers: undefined,
        site: '1',
        counter: 0,
        total: 4,
        socialQuizResult: undefined
    };

    componentDidMount() {
        this.quizAjaxCall();
    }

    /**
     * Save chosen answers to get the tag
     */
    handleAddAnswer = (answer, answerId) => {
        // answer = answer.replace('/', ' ');
        
        if(answer == 'sticker-ul de perete perfect'){
            this.setState((prevState) => ({ answers: 'sticker' }));
            this.setState((prevState) => ({counter: prevState.counter+2}));
        }else if(answer == 'decorul de perete perfect' || answer == 'standard'){
            !this.state.answers ? this.setState({answers: ' '}) : this.setState((prevState) => ({ answers: prevState.answers.concat(" " + answer) }));
            this.setState((prevState) => ({counter: prevState.counter+2}));
        }else{
            !this.state.answers ? this.setState({answers: ' '}) : this.setState((prevState) => ({ answers: prevState.answers.concat(" " + answer) }));
            this.state.counter<this.state.total && this.setState((prevState) => ({ counter: prevState.counter+1}));
        }

        this.quizAjaxCall(answerId);

    }

    // http://socialeyes20180319122912.azurewebsites.net/api/Counter
    // data: ip, link
    quizAjaxCall(answerId='prima') {
        this.setState({loading: true});
        axios.get('http://socialeyes20180319122912.azurewebsites.net/api/Quizz?site=1&id=' + answerId)
            .then(res => {
                this.setState({ socialQuizResponse: res.data, loading: false });
            })
    }

    render() {
        const { error, title, socialQuizResponse, loading, questionContent, answerContent, counter, total } = this.state;

        if (error) {
            return <div>Error: {error.message}</div>;
        } else {


            const questionContent = socialQuizResponse["Intrebare: "];
            const answerContent = socialQuizResponse["Raspunsuri: "];

            return (
                <div style={this.props.style} className={styles.socialQuizContainer}>
                    <ProgressBar counter={counter} total={total}/>

                    <div className={styles.questionAnswerContainer}>
                        {loading ? <LoadingSpinner /> : <QuizQuestion content={questionContent} />}
                        {/* for loop - number of answers */}
                        {console.log(answerContent)}
                            {
                                socialQuizResponse == 'Nu exista' ? <QuizResults result={this.state.answers}/> :
                                <div className={styles.answers}>
                                    {!loading && answerContent.map((option, index) => (//change this also!!!!!!!!!!!!!!
                                        <div key={index}>
                                            <QuizAnswer handleAddAnswer={this.handleAddAnswer} answerValue={Object.values(option)} answerId={Object.keys(option)} checkmark={this.state.checkmark} />
                                        </div>
                                    ))}
                                </div>
                            }
                    </div>
                </div>
            );
        }
    }
}