import React from "react";
import axios from 'axios';
import { CSSTransitionGroup } from 'react-transition-group'
import RenderSocialQuiz from './RenderSocialQuiz';
import styles from "../styles/components/_quizContainer.scss";

const showPopup = {
    transform: 'translate(-85vw, 0)'
};

const hidePopup = {
    transform: 'translate(0, 0)'
};

export default class SocialQuiz extends React.Component {

    constructor() {
        super();
        this.state = {
            isHidden: true,
            toggleShow: 'translate(0, 0)',
            containerVisibility: 'hidden',
            containerDisplay: 'none',
            ip: ''
        };
    }

    getIp = () => {
        axios.get('https://jsonip.com')
            .then(res => {
                // console.log(res.data.ip);
                this.setState({ ip: res.data.ip});
                this.counterAjaxCall();
                console.log(res);
            })
            .catch(function (error) {
                console.log(error);
            });
    }

    counterAjaxCall = () => {
        console.log(this.state.ip);
        axios.post('https://socialeyes20180319122912.azurewebsites.net/api/Counter?ip=' + this.state.ip + '&link=null')
        .then(function (response) {
            console.log(response);
        })
        .catch(function (error) {
            console.log(error);
        });
    }

    escFunction = (event) => {
        if(event.keyCode === 27) {
            //Do whatever when esc is pressed
            this.onHide();
        }
    }

    onShow = () => {
        this.setState({containerVisibility: 'visible', containerDisplay: 'block', toggleShow: 'translate(-85vw, 0)', isHidden: false});
        document.addEventListener("keydown", this.escFunction, false);
    };

    onHide = () => {
        this.setState({toggleShow: 'translate(0, 0)', isHidden: true, containerVisibility: 'hidden', containerDisplay: 'none'});
        document.removeEventListener("keydown", this.escFunction, false);
    };

    toggleQuizPopup = () => {
        this.getIp();
        this.state.isHidden ? this.onShow() : this.onHide();
    };

    render() {
        return (
            <div style={{overflow: 'hidden', paddingBottom: '2em'}}>
                {
                    <img style={{transition: 'all 0.8s ease-out', transform: this.state.toggleShow}} className={styles.startQuiz} src={"https://social-eyes.ro/images/hide-man.png"} alt="" onClick={this.toggleQuizPopup} />
                }
                <RenderSocialQuiz style={{transition: 'all 0.8s ease-out', transform: this.state.toggleShow, visibility: this.state.containerVisibility}} />
            </div>
        );
    }
}