import React from 'react';
import styles from "../styles/components/_quizAnswer.scss";

/**
 * Answer content component
 *
 * renders an answer button for the current question
 */
export default class QuizAnswer extends React.Component {

    state = {
        checkmark: '#D0EED3'
    };

    /**
     * Send chosen answers to SocialQuiz component
     */
    handleAddAnswer = (value) => {
        this.props.handleAddAnswer(this.props.answerValue.toString().toLowerCase(), this.props.answerId);
    };

    mouseEnter = () => {
        this.setState({checkmark: '#2F4A2F'});
    };

    mouseLeave = () => {
        this.setState({checkmark: '#D0EED3'});
    };

    render() {

            return (
                <div key={this.props.answerId} className={styles.socialQuizAnswer}>
                    <span style={{background: this.state.checkmark}} className={styles.checkmark} onMouseEnter={this.mouseEnter} onMouseLeave={this.mouseLeave}></span>
                    <label className={styles.socialQuizAnswerChoice} onClick={this.handleAddAnswer} onMouseEnter={this.mouseEnter} onMouseLeave={this.mouseLeave}>{this.props.answerValue}</label>
                </div>
            )
    }
}
